.HomePage {
  max-width: 900px;
  width: 100%;
  margin: 0 auto 40px;
  padding: 0 40px;
}

  .HomePage > h1 {
    text-align: center;
  }

  .HomePage__refreshButton {
    margin-bottom: 14px;
  }

  .HomePage__mainContent {
    display: -webkit-box;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
            flex-direction: column;
    -webkit-box-align: center;
            align-items: center;
  }
