.Button {
  outline: none;
  border: 1px solid transparent;
  border-radius: 4px;
  padding: 6px 32px;
  position: relative;
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
  cursor: pointer;
  text-align: center;
  -webkit-transition:
    background-color .2s ease-out,
    color .2s ease-out;
  transition:
    background-color .2s ease-out,
    color .2s ease-out;
  border-radius: 5.5px;
  background-color: white;
  display: -webkit-inline-box;
  display: inline-flex;
  -webkit-box-pack: center;
          justify-content: center;
  -webkit-box-align: center;
          align-items: center;
}

  .Button--disabled,
  .Button:disabled {
    cursor: not-allowed;
  }

  .Button--default {
    background-color: white;
    border-color: transparent;
    color: #222;
    font-family: 'Open Sans', Helvetica, sans-serif;
    box-shadow: 0 4px 10px 0 rgba(20,9,62,0.09);
  }

  .Button--default:hover {
      border-color: none;

    background-color: rgb(230, 230, 230);
    color: #222;
    text-decoration: none;
  }

  .Button--default:focus {
      border-color: none;

    background-color: rgb(204, 204, 204);
    color: #222;
    text-decoration: none;
    outline: none;
  }

  .Button--default--disabled,
  .Button--default:disabled {
    background-color: #ddd;
    border-color: transparent;
    color: #484848;
    -webkit-filter: grayscale(1);
            filter: grayscale(1);
  }

  .Button--primary {
    background-color: #635ada;
    border-color: transparent;
    color: white;
    font-family: 'Open Sans', Helvetica, sans-serif;
  }

  .Button--primary:hover {
      border-color: none;

    background-color: rgb(89, 81, 196);
    color: white;
    text-decoration: none;
  }

  .Button--primary:focus {
      border-color: none;

    background-color: rgb(79, 72, 174);
    color: white;
    text-decoration: none;
    outline: none;
  }

  .Button--primary--disabled,
  .Button--primary:disabled {
    background-color: #ddd;
    border-color: #1f86db;
    color: #484848;
    -webkit-filter: grayscale(1);
            filter: grayscale(1);
  }

  .Button--secondary {
    background-color: white;
    border-color: #f2f2f8;
    color: #1200FF;
    font-family: 'Open Sans', Helvetica, sans-serif;
  }

  .Button--secondary:hover {
      border-color: rgb(218, 218, 223);

    background-color: rgb(230, 230, 230);
    color: #1200FF;
    text-decoration: none;
  }

  .Button--secondary:focus {
      border-color: rgb(182, 182, 186);

    background-color: rgb(204, 204, 204);
    color: #1200FF;
    text-decoration: none;
    outline: none;
  }

  .Button--secondary--disabled,
  .Button--secondary:disabled {
    background-color: #ddd;
    border-color: transparent;
    color: #484848;
    -webkit-filter: grayscale(1);
            filter: grayscale(1);
  }

  .Button--link {
    border: none;
    padding: 0;
    background: inherit;
    font-weight: bold;
    font-size: inherit;
    line-height: inherit;
    color: #555;
  }

  .Button--link:hover {
      color: black;
    }

  .Button--link:focus {
      text-decoration: underline;
    }

  .Button--unstyled {
    border: none;
    padding: 0;
    background: none;
    font-size: inherit;
    font-weight: inherit;
    text-align: left;
    position: static;
    box-shadow: inherit;
  }

  .Button--fab {
    border-radius: 50%;
    width: 80px;
    height: 80px;
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
            align-items: center;
    -webkit-box-pack: center;
            justify-content: center;
    background-color: white;
    padding: 0;
    padding: initial;
  }

  .Button__loader {
    position: absolute;
    right: 8px;
    top: calc(50% - 9px);
  }
